import React, {Component} from "react";
import PropTypes from 'prop-types';

export default class BuyMoreProductsComponent extends Component {

    constructor(props){
        super(props);
        this.addButtonClicked = this.addButtonClicked.bind(this)
    }

    addButtonClicked(e){
        e.preventDefault()
        this.props.addButtonClicked()
    }

    render() {
        return (
            <a href="#" aria-label={this.props.buyMoreLabel} onClick={this.addButtonClicked} className={this.props.className}>
                <div className="u-position-relative u-box-shadow-hover u-box-shadow--s u-margin-top-l g-white1-bg">
                    <div className="u-padding-left-l u-padding-right-l ">
                    <table className={"u-table-fixed" + (this.props.topBorder ? " u-large-border-top u-medium-border-top" : "")}>
                            <tbody>
                            <tr>
                                <td className="opl-checkout__icon__cell"><span className={"g-icon g-icon--only  g-icon--s g-icon--"+this.props.icon}></span></td>
                                <td className="u-padding-top-l u-padding-l u-padding-right-l u-font-bold">{this.props.buyMoreLabel}</td>
                                <td className="opl-checkout__button__cell u-padding-top-l u-padding-l">
                                    <div className="opl-product-addlink u-right u-small-right"/>
                                </td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </a>
        );
    }
}

BuyMoreProductsComponent.propTypes = {
    buyMoreLabel: PropTypes.string,
    icon: PropTypes.string,
    addButtonClicked: PropTypes.func
}

BuyMoreProductsComponent.defaultProps = {
    buyMoreLabel: "Dobierz produkt",
    icon: "machines",
    topBorder: false
}

